<script>
import appConfig from '@src/app.config'
import Layout from '@layouts/main'
import PageHeader from '@components/page-header'
import Stat from '@components/widget-stat'
import axios from 'axios';
import Utils from '../../../../utils/Util';

/**
 * Invoice component
 */
export default {
	page: {
		title: 'Dashboard',
	},
	components: { Layout, PageHeader, Stat },
	data() {
		return {
			api: Utils.Host(),
			title: 'Dashboard',
			otoritasweb: [
			],
			user: [
			],
			userotoritas: [
			],
		}
	},
	mounted() {
      console.log("alamat", Utils.Host())
      this.Getdataotoritasweb()
    },
	methods: {
      async Getdataotoritasweb() {
        try {
            let response1 = await axios.get(this.api+"/dashboard/otoritasweb", Utils.authHeader());
			for (const tampil of response1.data) {
				tampil.title = 'Otoritas - ' + tampil.title;
				tampil.icon = 'file-text';
				tampil.color = 'primary';
			}
            this.otoritasweb = response1.data;
			let response2 = await axios.get(this.api+"/dashboard/user", Utils.authHeader());
			for (const tampil of response2.data) {
				tampil.title = tampil.title == '1' ?  'User - Tidak Aktif' : 'User - Aktif'
				tampil.icon = 'users';
				tampil.color = 'success';
			}
			this.user = response2.data;
			let response3 = await axios.get(this.api+"/dashboard/userotoritas", Utils.authHeader());
			for (const tampil of response3.data) {
				tampil.title = 'Otoritas User Website - '+tampil.title
				tampil.icon = 'users';
				tampil.color = 'warning';
			}
            this.userotoritas = response3.data;
        } catch (err) {
            console.error(err);
        }
      },
	}
}
</script>

<template>
	<Layout>
		<PageHeader :title="title" :items="items" class="d-print-none" />
		<div class="row">
			<div class="col-12">
				<div class="card">
					<div class="card-body">
						<!-- Logo & title -->
						
						<div class="row">
							<div class="col-12">
								<div class="table-responsive">

									<div class="row">
									<div v-for="stat of otoritasweb" :key="stat.title" class="col-md-6 col-xl-3">
										<Stat
											:title="stat.title"
											:value="stat.value"
											:icon="stat.icon"
											:color="stat.color"
										/>
									</div>
									</div>

									
									<br>
									<div class="row">
									<div v-for="stat of userotoritas" :key="stat.title" class="col-md-6 col-xl-3">
										<Stat
											:title="stat.title"
											:value="stat.value"
											:icon="stat.icon"
											:color="stat.color"
										/>
									</div>
									</div>

									<br>
									<div class="row">
									<div v-for="stat of user" :key="stat.title" class="col-md-6 col-xl-3">
										<Stat
											:title="stat.title"
											:value="stat.value"
											:icon="stat.icon"
											:color="stat.color"
										/>
									</div>
									</div>
									
									

								</div>
								<!-- end table-responsive -->
							</div>
							<!-- end col -->
						</div>
						<!-- end row -->

	
					</div>
				</div>
			</div>
			<!-- end col -->
		</div>
		<!-- end row -->
		<!-- end row -->
	</Layout>
</template>
